html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  background: #0C1621;
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  font-weight: 400;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  display: block;
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

.btn-main {
  display: flex;
  align-items: center;
  gap: 5px;
  width: max-content;
  padding: 11px 35px;
  font-size: 16px;
  color: var(--text-white-0);
  background-color: #196264;
  background-image: url("../../img/bg-btn.svg"), linear-gradient(to bottom, #207278, #237b81, #26838a, #298c94, #2c959d);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  box-shadow: 0 1.33px 2.67px 0 rgba(29, 255, 255, 0.55);
  box-shadow: 0 0 0 4px rgba(28, 255, 255, 0.17);
  border: 2px solid rgba(255, 255, 255, 0.21);
  border-radius: 85px;
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  transition: 0.05s ease all;
}
.btn-main img {
  width: 25px;
