.levels {
  padding: var(--offset-padding) 0;
  background-image: url(../../img/bg-levels.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;

  &__subtitle {
    text-align: center;
    margin-top: 10px;
  }

  &__title {
    text-align: center;
  }

  &__descr {
    color: #fff;
    font-size: 18px;
    text-align: center;
    margin-top: 10px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 45px;
    margin-top: 85px;
  }

  &__item {
    border-radius: 18px;
    background-color: #181E2A;
    padding: 25px;
    display: flex;
    flex-direction: column;
    border: 0.7px solid #E7EBFF;
    box-shadow: 0 17px 27px 0 rgba(188, 202, 255, 0.13);
    justify-content: space-between;
  }

  &__top{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
  }

  &__bottom{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 20px;
    margin-top: 20px;
  }

  &__price {
    border-bottom: 1px solid #E7EBFF;
    padding-bottom: 20px;
  }

  &__icon {
    width: 38px;
    height: auto;
    display: block;
    object-fit: contain;
    margin-bottom: 14px;
  }

  &__head {
    margin-bottom: 9px;
    color: #fff;
    font-size: 28px;
    text-align: left;
    font-weight: 600;
  }

  &__subdescr {
    color: #fff;
    font-size: 16px;
    margin-bottom: 16px;
    font-weight: 300;
  }

  &__price-cost {
    font-family: "Outfit", sans-serif;
    font-weight: 500;
    font-size: 40px;
    color: #fff;
    margin-right: 7px;
  }

  &__price-month {
    font-size: 16px;
    color: #fff;
    font-weight: 300;
  }

  &__sublist {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }

  &__subitem {
    color: #fff;
    display: flex;
    align-items: flex-start;
    gap: 5px;
    font-weight: 500;
  }

  &__btn {
    font-weight: 500;
    font-size: 16px;
    color: #fff;
    display: block;
    width: 100%;
    text-align: center;
    border-radius: 8px;
    border: 0.7px solid #fff;
    padding: 15px 0;
    background-color: #181E2A;
    transition: 0.05s ease-in all;

    &:hover {
      background-color: #2c3550;
      box-shadow: 0 4px 12px rgba(255, 255, 255, 0.3);
      transform: translateY(-3px);
      transition: 0.05s ease-in all;
    }

    &:active {
      transform: translateY(0);
      box-shadow: none;
      transition: 0.05s ease-in all;
    }
  }

  &__vip {
    background-image: url(../../img/bg-vip.svg);
    background-size: 134% 120%;
    background-repeat: no-repeat;
    background-position: center;
  }

  &__vip &__head {
    color: #1B223C;
  }

  &__vip &__subdescr {
    color: #3C3C3C;
  }

  &__vip &__subitem {
    color: #1B223C;
  }

  &__vip &__price-cost {
    color: #1B223C;
  }

  &__vip &__price-month {
    color: #3C3C3C;
  }

  &__vip &__sublist {
    color: #1B223C;
  }

  &__vip &__btn {
    background-color: #1B223C;
    color: #fff;
    border: 1px solid #152C80;

        &:hover {
      background-color: darken(#1B223C, 10%);
      box-shadow: 0 4px 12px rgba(21, 44, 128, 0.7); // чуть синий свет от бордера
      transform: translateY(-3px);
    }

    &:active {
      transform: translateY(0);
      box-shadow: none;
    }
  }
}
