.header {

  &__container {
    position: fixed;
    z-index: 999;
    top: 15px;
    left: 50%;
    transform: translateX(-50%);
    max-width: 1440px;
    padding: 10px 15px 10px 15px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: 1px solid #37393C;
    box-shadow: 0 1px 4px 0 rgba(38, 40, 46, 0.37);
    border-radius: 60px;
    background: rgba(14, 59, 68, 0.8);
    backdrop-filter: blur(4px);
    -webkit-backdrop-filter: blur(4px);
  }

  &__reg {
    font-size: 14px;
  }

  &__nav {
    display: flex;
    list-style-type: none;
    gap: 30px;
  }

  &__nav-link {
    color: var(--text-color);
    font-size: 16px;
    font-family: "Raleway", sans-serif;
    font-weight: 400;
    transition: 0.05s ease-in all;

    &:hover {
      color: #fff;
      transition: 0.05s ease-in all;
    }
  }

  &__right {
    display: flex;
    align-items: center;
    gap: 22px;
  }

  &__btns {
    display: flex;
    align-items: center;
    gap: 22px;
  }

  &__login {
    padding: 13px 43px;
    font-size: 14px;
    color: var(--text-white-0);
    border: 2px solid rgba(255, 255, 255, 0.21);
    border-radius: 40px;
    box-shadow: 0 0 2px 0 rgba(29, 255, 255, 0.55);
    font-family: "Raleway", sans-serif;
    font-weight: 400;
    transition: 0.05 ease-in all;

    &:hover {
      color: #1cfffd;
      border-color: #1cfffd;
      box-shadow: 0 0 8px 2px #1cfffd;
      transform: scale(1.05);
      transition: 0.05 ease-in all;
    }
  }

  &__logo {

    & img {
      height: 40px;
      width: auto;
      object-fit: contain;
    }
  }
}

.menu-btn {
  color: #fff;
}
