.about {

  background-image: url(../../img/bg-about.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  padding: var(--offset-padding) 0;

  &__wrap {
    display: flex;
    align-items: flex-start;
    gap: 50px;
  }

  &__left {
    width: 50%;
  }

  &__right {
    width: 50%;
  }

  &__descr {
    color: #fff;
    font-size: 16px;
    margin: 10px 0;
    font-family: "Montserrat", sans-serif;
    font-weight: 400;
  }

  &__descr-2 {
    color: #DADADA;
    font-size: 16px;
    margin-bottom: 30px;
  }

  &__info {
    gap: 130px;
    display: flex;
    align-items: flex-start;
    margin-bottom: 40px;
  }

  &__info-title {
    font-size: 16px;
    color: #fff;
    margin-bottom: 12px;
    display: block;
  }

  &__info-number &__info-descr {
    display: block;
    color: #fff;
    font-size: 30px;
    font-weight: 600;
  }

  &__info-adress &__info-descr {
    display: flex;
    align-items: flex-start;
    gap: 10px;

    & span {
      color: #fff;
      font-size: 18px;
    }
  }

  &__certificate {
    display: flex;
    align-items: center;
    gap: 60px;
    padding: 25px 20px;
    border-radius: 20px;
    background-color: rgba(255, 255, 255, 0.05);
          transition: 0.05s ease-in all;

    &:hover{
      background-color: rgba(255, 255, 255, 0.1);
      transform: scale(1.01);
      transition: 0.05s ease-in all;
    }
  }

  &__certificate-title {
    display: flex;
    gap: 10px;
    align-items: center;
  }

  &__certificate-title {

    & span {
      display: block;
      color: #fff;
      font-size: 22px;
      font-weight: 600;
    }

    & img {
      width: 20px;
      height: auto;
    }
  }

  &__certificate-descr {
    text-align: left;
    margin-top: 10px;
    color: #B1B5C3;
    font-size: 14px;
    font-weight: 300;
    padding-left: 31px;
  }

  &__btn {
    margin: 0 auto;
    margin-top: 60px;
  }
}

.offer {

  &__top {
    display: flex;
    align-items: center;
    gap: 35px;
    max-width: 820px;
    justify-content: center;
    margin: 0 auto;

    & img{
      width: 160px;
      height: auto;
    }
  }

  &__title {
    font-size: 45px;
    font-weight: 500;

    & b {
      color: #0BCACA;
    }
  }

  &__descr {
    font-size: 16px;
    color: #fff;
    text-align: center;
    max-width: 1000px;
    margin: 15px auto 50px auto;
    font-weight: 400;
  }

  &__wrap {
    display: flex;
    gap: 50px;
  }

  &__wrap-left{
    width: 50%;
  }

  &__wrap-right{
    width: 50%;
  }

  &__titles{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 50px;
  }

  &__subtitle {
    font-size: 30px;
    color: #fff;
    text-align: center;
    font-weight: 500;
  }

  &__list {
    padding: 50px 35px;
    background-color: rgba(255, 255, 255, 0.05);
    border-radius: 30px;
    color: #FFF;
    margin-top: 20px;
  }

  &__item {
    display: flex;
    align-items: flex-start;
    gap: 10px;

    &:not(:last-child) {
      margin-bottom: 10px;
    }
  }

  &__check{
    width: 22px;
    height: auto;
  }

  &__text {
    font-size: 18px;
    color: #F0F0F0;
    font-weight: 400;
  }

  &__btn {
    margin: 0 auto;
    margin-top: 60px;
  }
}
