.works {

  background-image: url(../../img/bg-works.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  padding: var(--offset-padding) 0;

  &__works {
    font-weight: 500;
  }

  &__descr {
    color: #fff;
    font-weight: 400;
    font-size: 18px;
    margin-top: 15px;
  }

  &__swiper {
    margin-top: 50px;
    padding-bottom: 50px !important;

    & .works__wrapper {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 16px;
    }

    & .swiper-pagination-bullet{
      background-color: #B8B8B8;
      width: 10px;
      height: 10px;
    }

    & .swiper-pagination-bullet-active{
      width: 24px;
      height: 10px;
      background-color: #0BCACA;
      border-radius: 5px;
    }
  }

  &__head {
    font-weight: 500;
    color: #fff;
    font-size: 22px;
  }

  &__subdescr {
    color: #fff;
    font-size: 16px;
    font-weight: 400;
    text-align: center;
  }

  &__wrap {
    position: relative;
  }

  &__arrow {
    position: absolute;
    bottom: 0;
    right: 0;
    border-radius: 100%;
    background-color: #2C353A;
    padding: 14px;
    transition: 0.05s ease-in all;
  }

  &__wrapper:hover &__arrow {
    background-color: #0BCACA;
    transition: 0.05s ease-in all;
  }
}
