.stages {

  background-image: url(../../img/bg-stages.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  padding: var(--offset-padding) 0;

  &__top {
    display: flex;
    gap: 30px;
    align-items: center;
    list-style-type: none;
    margin: 0;
    padding: 35px 0;
    background-image: url(../../img/Description.svg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
  }

  &__subtitle {
    margin-top: 50px;
  }

  &__descr {
    color: #fff;
    font-size: 18px;
    font-weight: 400;
  }

  &__top-item{
    cursor: pointer;
  }

  &__top-item img {
    transition: 0.05 ease-in all;
  }

  &__top-item:hover img {
    opacity: 0.8;
    transition: 0.05 ease-in all;
  }
}

.process {
  padding: 0;
  margin: 0;
  margin-top: 50px;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 20px;
  row-gap: 100px;
  list-style-type: none;

  &__item .process__wrap {
    border-radius: 30px;
    padding: 30px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
  }

  &__item:nth-child(1) {
    grid-column: 1 / 5;
    margin-top: 100px;

    & .process__descr {
      margin-top: 16px;
    }

    & .process__wrap {
      background-image: url(../../img/process-1.png);

      & img {
        width: 250px;
        margin: 0 auto;
      }

      & img:nth-child(2) {
        margin-top: 10px;
        margin-bottom: 40px;
      }
    }
  }

  &__item:nth-child(2) {
    grid-column: 5 / 10;
    margin: 0 50px;

    & .process__wrap {
      padding: 120px 45px 60px 45px;
      background-image: url(../../img/process-2.png);

      & img {
        width: 280px;
        margin: 0 auto;
      }

      & .proccess__img {
        margin-top: 25px;
        margin-bottom: 80px;
      }

      & .process__wrapper {
        position: relative;
        margin: 0 auto;
        width: max-content;

        & img {
          margin: 0;
        }
      }

      & .process__img-5k {
        position: absolute;
        bottom: 60px;
        right: -60px;
        width: 100px;
      }

      & .process__descr {
        margin-top: 16px;
      }
    }
  }

  &__item:nth-child(3) {
    grid-column: 10 / 13;
    margin-top: 200px;

    & .process__wrap {
      background-image: url(../../img/process-3.png);

      & img {
        width: 250px;
        margin: 0 auto;
      }

      & img:nth-child(2) {
        margin-top: 10px;
        margin-bottom: 40px;
      }
    }

    & .process__title {
      font-size: 22px;
    }

    & .process__descr {
      margin-top: 16px;
      font-size: 14px;
    }
  }

  &__item:nth-child(4) {
    grid-column: 1 / 7;
    margin-top: 50px;

    & .process__wrap {
      background-image: url(../../img/process-4.png);

      & img:nth-child(2) {
        margin-top: 10px;
        margin-bottom: 60px;
      }

      & .process__title {
        text-align: left;
        margin-bottom: 25px;
      }

      & .process__descr {
        text-align: left;
      }
    }
  }

  &__item:nth-child(5) {
    grid-column: 8 / 12;
    position: relative;

    & .process__wrap {
      background-image: url(../../img/process-5.png);

      & .procces__text {
        position: absolute;
        bottom: 20px;
        left: -70px;
        background-color: #196264;
        border-radius: 10px;
        width: 350px;
        padding: 16px 20px;
      }

      & .process__title {
        font-size: 24px;
        color: #fff;
      }

      & img:nth-child(1) {
        position: relative;
        top: -55px;
        left: 50%;
        transform: translateX(-50%);
      }

      & img:nth-child(2) {
        position: absolute;
        bottom: 40%;
        transform: translateY(40%);
        right: -35px;
      }
    }
  }

  &__title {
    font-size: 30px;
    font-weight: 600;
    color: #fff;
    text-align: center;
  }

  &__descr {
    color: #fff;
    font-size: 16px;
    font-weight: 500;
    text-align: center;
  }
}
